import React, {FC, SetStateAction, MouseEvent, useEffect, useState} from 'react';
import Button from "../UI/Button/Button";
import styles from './numpad.module.scss'
import {count, isNum} from "../../utils";
import backspace from '../../assets/backspace.png'
import divided from '../../assets/divided.png'
import moreFunc from '../../assets/more-func.png'


interface NumpadProps {
    screen:string
    setScreen:(setScreen:SetStateAction<string>) => void
    theme:boolean
}

const Numpad:FC<NumpadProps> = ({setScreen,screen,theme}) => {

    const [currentNum,setCurrentNum] = useState<string>('')
    const [prevNum,setPrevNum] = useState<string>('')
    const [operation,setOperation] = useState<string>('')
    const MAX_LENGTH = 10

    useEffect(()=>{
        count(screen)
    },[screen])

    useEffect(()=>{
        percentToScreen()
    },[currentNum])

    const addToScreen = (e:MouseEvent<HTMLButtonElement, globalThis.MouseEvent>) => {
        const {button} = e.currentTarget.dataset
        if (screen.length === MAX_LENGTH){
            return false
        }
        if (isNum(button ?? '')){
            setCurrentNum(prev => prev+button)
            setScreen(prev => prev + button)

        }else if (button === '%' && currentNum){
            setOperation('%')
            setScreen(screen.slice(0,-currentNum.length))
            getPercent()
        } else if (button === '-'){
            setScreen(button)
        } else if (currentNum && !isNum(screen[screen.length -1]) && !isNum(button ?? '')){
                setScreen(prev => prev.slice(0,-1) + button)
        }else if (currentNum){

            setScreen(prev => prev + button)
            setPrevNum(eval(screen))
            setCurrentNum('')
        }
    }

    const clearScreen = () => {
        setScreen( '')
        setCurrentNum('')
        setPrevNum('')
    }
    const removeLast = () => {
        if (currentNum){
            setCurrentNum(prev => prev.slice(0,-1))

        } else if (isNum(screen[screen.length - 1])){
            setPrevNum(prev =>  String(Number(prev) -  Number(screen[screen.length - 1])))

        }
        setScreen(screen.slice(0,-1))
    }

    const resultToScreen = () => {
        if (screen){
            setScreen(String(count(screen)))
        }
    }
    const getPercent = () =>{
        prevNum
            ? setCurrentNum(prev =>String(Number(prev) / 100 * Number(prevNum)))
            : setCurrentNum(prev => String(+prev / 100))
    }

    const percentToScreen = () =>{
        if (currentNum && operation === '%'){
            getPercent()
            setScreen(prev => prev + currentNum)
            setOperation('')


        }
    }

    return (
        <div className={styles.numpad}>
            <Button click={clearScreen} operation={true} theme={theme} >AC</Button>
            <Button click={removeLast} operation={true} ><img src={backspace} alt=""/></Button>
            <Button click={addToScreen} operation={true} data={'%'}>%</Button>
            <Button click={addToScreen} operation={true} data={'/'}><img src={divided} alt=""/></Button>
            <Button click={addToScreen} data={'7'} theme={theme}>7</Button>
            <Button click={addToScreen} data={'8'} theme={theme}>8</Button>
            <Button click={addToScreen} data={'9'} theme={theme}>9</Button>
            <Button click={addToScreen} operation={true} data={'*'}>x</Button>
            <Button click={addToScreen} data={'4'} theme={theme}>4</Button>
            <Button click={addToScreen} data={'5'} theme={theme}>5</Button>
            <Button click={addToScreen} data={'6'} theme={theme}>6</Button>
            <Button click={addToScreen} operation={true} data={'-'}>-</Button>
            <Button click={addToScreen} data={'1'} theme={theme}>1</Button>
            <Button click={addToScreen} data={'2'} theme={theme}>2</Button>
            <Button click={addToScreen} data={'3'} theme={theme}>3</Button>
            <Button click={addToScreen} operation={true} data={'+'}>+</Button>
            <Button click={addToScreen} operation={true}><img src={moreFunc}/></Button>
            <Button click={addToScreen} data={'0'} theme={theme}>0</Button>
            <Button click={addToScreen} data={'.'} theme={theme}>.</Button>
            <Button click={resultToScreen} equal={true}>=</Button>
        </div>
    );
};
export default Numpad;