export const count = (screen:string):string => {
    if (isNum(screen[screen.length - 1])){
        return eval(screen)

    }else if (screen[0] === '-' && screen.length <2){
        return '0'
    }else {
        return eval(screen.slice(0, -1))
    }

}

export const isNum = (num:string):boolean => {
    return !!num === /\d/.test(num);
}